* {
  margin: 0;
  padding: 0;
}

body {
  background-color: #020631;
}

.app {
  position: absolute;
  top: 10%;
  left: 50%;
  transform: translate(-50%, -10%);
}

.mainHeading {
  color: white;
}

.app h1,
h2 {
  color: white;
}

.input {
  display: flex;
  align-items: center;
  display: flex;
  margin-top: 30px;
}
input {
  margin-top: 0.8em;
  width: 20em;
  height: 2em;
  background-color: white;

  align-items: center;
  border-radius: 6px;
  padding: 5px;
}

.fa-plus {
  cursor: pointer;
  font-size: larger;
  font-weight: 900;
  color: white;
  margin-right: 5px;
}
.container {
  padding: 20px;
  margin-left: 500px;
  width: 400px;
  height: 600px;
  background-color: #020631;
}
.fa-trash {
  cursor: pointer;
  font-size: larger;
  font-weight: 900;
  color: white;
  margin-right: 5px;
}
.inputtwo {
  top: -30px;
  left: 30px;
  font-weight: bold;
}
.remove {
  width: 100px;
  height: 40px;
  margin-top: 10px;
  border: solid 1px blue;
  border-radius: 5px;
}
.removebtn {
  padding-left: 20%;
}
.remove:hover {
  color: white;
  background-color: blue;
}
#secondinput:hover {
  color: white;
  background-color: blue;
}
